.mainBlock {
    min-height: 100vh;
    background-color: burlywood;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.text {
    max-width: 100%;
    display: flex;
    width: 300px;
    height: 400px;
    background-color: darkgrey;
    max-height: 100%;
    overflow-wrap: break-word;
    justify-content: space-around;
}

.foto {
    max-width: 100%;
    height: 400px;
    width: 300px;
    background-color: #f0b2c6;
}

.textAbout {
    min-width: 100%;
    max-height: 100%;
    overflow-wrap: break-word;
}


